import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Form, Input, Row, Col, Select, Switch, Button} from 'antd'
import {
    CheckOutlined,
    CloseOutlined,
    SearchOutlined
} from '@ant-design/icons'

import Flex from "../../../../components/shared-components/Flex";
import {_getQueueRoomsListFiltered, GetDistrictsKarawang} from "../../../../redux/actions";


const CompSearchRoom = props => {
    let {
        GetDistrictsKarawang,
        _getQueueRoomsListFiltered
    } = props
    const [form] = Form.useForm()

    useEffect(() => {
        GetDistrictsKarawang()
    }, [GetDistrictsKarawang])


    const handleLocations = (val, {item}) => {
        form.setFieldsValue({
            ['villa.districts']: item?._id,
        })
    }

    const onFinish = () => {
        form.validateFields()
            .then((values) => {
                _getQueueRoomsListFiltered({
                    limit: 20,
                    page: 1,
                    price_min:values?.price?.min ?? 0,
                    price_max:values?.price?.max ?? 2500000,
                    'currency[exists]':true,
                    'currency[in]':["IDR", 'USD', "Rp"],
                    'room_limit[$gte]':values?.limit ?? 1,
                    'unit[$exists]':true,
                    'unit[$nin]':["week", 'month', 'year'],
                    'villa[districts]':values.villa?.districts,
                    'ac[terms]': "$in",
                    'ac[value]': [values?.ac?.value],
                    'tv[terms]': "$in",
                    'tv[value]': [values?.ac?.value],
                    'wifi[terms]': "$in",
                    'wifi[value]': [values?.ac?.value],

                })
            })
    }

    return (
        <div className="w-full">
            <Form
                layout={"vertical"}
                initialValues={{
                    villa: {
                        districts: null,
                    },
                    price: {
                        min: 0,
                        max: 2500000
                    },
                    limit: 1,
                    ac:{
                        terms:"$in",
                        value:true
                    },
                    tv:{
                        terms:"$in",
                        value:true
                    },
                    wifi:{
                        terms:"$in",
                        value:true
                    },


                    facility: {
                        ac: false,
                        tv: false,
                        wifi: false,
                        bed_type: "single"
                    }
                }}
                onFinish={onFinish}
                form={form}
            >
                <Row gutter={24}>
                    <Col
                        xs={{
                            order: 1,
                            span: 24
                        }}
                        md={{
                            order: 1,
                            span: 8,
                        }}
                    >
                        {
                            !props?.loading && Array.isArray(props?.data) && props?.data.length > 0 && (
                                <Form.Item
                                    label={'Location'}
                                    name={['villa', 'districts']}
                                    rules={[
                                        {
                                            required: true,
                                            message: "Location is Required!"
                                        },
                                    ]}
                                >
                                    <Select defaultValue={props?.data[0]?.name ?? "-"} onChange={handleLocations}>
                                        {
                                            props?.data.map((item, index) => (
                                                <Select.Option value={item?._id} item={item}>
                                                    {item?.name?.toString()?.toLowerCase() ?? "-"}
                                                </Select.Option>
                                            ))
                                        }

                                    </Select>
                                </Form.Item>
                            )
                        }

                    </Col>
                    <Col
                        xs={{
                            order: 1,
                            span: 24
                        }}
                        md={{
                            order: 2,
                            span: 16,
                        }}
                    >
                        <Row gutter={24}>
                            <Col
                                md={{
                                    order: 1,
                                    span: 12,
                                }}
                            >
                                <Form.Item
                                    label={'Price'}
                                    name={['price', 'min']}>
                                    <Input/>
                                </Form.Item>
                            </Col>
                            <Col md={{
                                order: 2,
                                span: 12,
                            }}>
                                <Form.Item
                                    label={'Price'}
                                    name={['price', 'max']}>
                                    <Input/>
                                </Form.Item>
                            </Col>
                            <Col md={{
                                order: 3,
                                span: 4
                            }}>
                                <Form.Item name={'limit'} label={'Limit'}>
                                    <Select defaultValue={2} style={{width: 100}}>
                                        {[1, 2, 3, 4, 5, 6, 7, 8, 9].map((val) => (
                                            <Select.Option value={val}>{val}</Select.Option>
                                        ))}

                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col md={{
                                order: 4,
                                span: 20
                            }}>
                                <Flex alignItems={'center'} mobileFlex={false}>
                                    <Form.Item name={'bed_type'} label={'Bed Type'} className={'mr-4'}>
                                        <Select defaultValue={'single'}>
                                            <Select.Option value={'single'}>Single</Select.Option>
                                            <Select.Option value={'double'}>Double</Select.Option>
                                            <Select.Option value={'twin'}>Twin</Select.Option>
                                            <Select.Option value={'large'}>Large</Select.Option>
                                            <Select.Option value={'extra-large'}>Extra Large</Select.Option>
                                        </Select>
                                    </Form.Item>
                                    <Form.Item name={['ac','value']} label={'AC'} className={'mr-4'}>
                                        <Switch
                                            checkedChildren={<CheckOutlined/>}
                                            unCheckedChildren={<CloseOutlined/>}
                                            defaultChecked
                                        />
                                    </Form.Item>
                                    <Form.Item name={['tv','value']} label={'TV'} className={'mr-4'}>
                                        <Switch
                                            checkedChildren={<CheckOutlined/>}
                                            unCheckedChildren={<CloseOutlined/>}
                                            defaultChecked
                                        />
                                    </Form.Item>
                                    <Form.Item name={['wifi','value']} label={'Wifi'} className={'mr-4'}>
                                        <Switch
                                            checkedChildren={<CheckOutlined/>}
                                            unCheckedChildren={<CloseOutlined/>}
                                            defaultChecked
                                        />
                                    </Form.Item>
                                </Flex>
                            </Col>
                        </Row>
                    </Col>
                    <Col xs={{
                        order: 3,
                        span: 24
                    }} className={'text-right'}>
                        <Button
                            htmlType={'submit'}
                            icon={<SearchOutlined/>}
                            type={'primary'}>Cari</Button>
                    </Col>
                </Row>

            </Form>
        </div>
    )
}

CompSearchRoom.propTypes = {
    GetDistrictsKarawang: PropTypes.func.isRequired,
    _getQueueRoomsListFiltered: PropTypes.func.isRequired
}
CompSearchRoom.defaultProps = {}

export default connect(({Locations}) => {
    let {collections} = Locations
    let {districts} = collections
    return {...districts}
}, {GetDistrictsKarawang, _getQueueRoomsListFiltered})(React.memo(CompSearchRoom))
