import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

const HomeTravelRecomendations = (props)=> {
    return (
        <div className="w-full">
            <h1>Home Travel Recomendations</h1>
        </div>
    )
}
HomeTravelRecomendations.propTypes = {
    notIn: PropTypes.array.isRequired
}
HomeTravelRecomendations.defaultProps = {
    notIn: []
}

export default connect(()=> {
    return {}
},{})(React.memo(HomeTravelRecomendations))