import React, {useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import { Button,Tag,Badge } from 'antd'
import Slider from 'react-slick'
import {_getTravelNearby} from "../../../../redux/actions";
import {first} from "lodash";
import {Link} from "react-router-dom";
import Utils from "../../../../utils";

const HomeTravelNearby = (props) => {
    let {_getTravelNearby, lat, long, nearby} = props
    useEffect(() => {
        _getTravelNearby({
            lat,
            long,
            max: 1000000,//1000000

        }, {
            page: 1,
            limit: 10,
            notIn: props?.notIn ?? []
        })
    }, [_getTravelNearby])

    const settings = {
        dots:true,
        infinite:true,
        centerMode: true,
        centerPadding: '20px',
        slidesToShow: nearby?.data?.length > 5 ? 5 : 5,
        autoplay: true,
        autoplaySpeed: 2000,
        responsive: [
            {
                breakpoint: 768,
                settings: {
                    arrows: false,
                    centerMode: true,
                    centerPadding: '40px',
                    slidesToShow: 3
                }
            },
            {
                breakpoint: 480,
                settings: {
                    arrows: false,
                    centerMode: true,
                    centerPadding: '40px',
                    slidesToShow: 1
                }
            }
        ]
    };
    return (
        <div className="w-full space-y-4 my-10">
            <div className="flex items-center justify-between">
                <h3>{props?.title ?? "Travel Near By"}</h3>
                <Button size={'small'} type={'link'} ghost>View All</Button>
            </div>
            <div className="w-full h-full block">
                {/*<Slider {...settings} className={'multiple-items'}>*/}
                {/*    {*/}
                {/*        typeof (nearby?.data) !== "undefined" && Array.isArray(nearby?.data) &&*/}
                {/*        nearby.data.length > 0 &&*/}
                {/*        nearby?.data.map((item, index) => {*/}
                {/*            return (*/}
                {/*                <div className={'w-full min-w-[220px] h-[320px] px-[10px]'} key={`index-card-nearby-${index}`}>*/}
                {/*                    <div className="w-full h-full bg-white rounded-xl">*/}

                {/*                    </div>*/}
                {/*                </div>*/}
                {/*            )*/}
                {/*        })*/}
                {/*    }*/}
                {/*</Slider>*/}
                <div className="flex items-center gap-6 overflow-x-auto pb-4">
                    {
                        typeof (nearby?.data) !== "undefined" && Array.isArray(nearby?.data) &&
                        nearby.data.length > 0 &&
                        nearby?.data.map((item, index) => {
                            let images = ''
                            if(typeof(item?.photo) !== "undefined" && Array.isArray(item?.photo) && item?.photo.length > 0){
                                images = [item?.photo[0]?.prefix,item?.photo[0]?.url].join("")
                            }
                            return (
                                <div className={'w-full min-w-[220px]  max-w-[220px] h-[320px]'} key={`index-card-nearby-${index}`}>
                                    <div className="w-full h-full bg-white rounded-xl overflow-hidden relative">
                                        <img
                                            alt={images}
                                            className={'w-full h-full object-cover transition duration-200 hover:scale-110 transform z-[1]'}
                                            src={images}
                                        />
                                        <div className="absolute bottom-0 left-0 py-4 w-full bg-gradient-to-t from-black p-2 space-y-3">
                                            <div>
                                                {
                                                    typeof(item?.categories) !== "undefined" && Array.isArray(item?.categories) && item?.categories?.length > 0 && (
                                                        <div className="w-full overflow-hidden flex items-center gap-3">
                                                            {
                                                                item?.categories?.slice(0,2)?.map((cItem)=> {
                                                                    return (
                                                                        <Badge
                                                                            className={'whitespace-nowrap '}
                                                                            color={cItem?.background ?? "#fff"}
                                                                            text={cItem?.name ?? "-"}
                                                                            key={cItem?._id}
                                                                            style={{color:"#fff"}}
                                                                        />
                                                                    )
                                                                })
                                                            }
                                                            {
                                                                item?.categories?.slice(2)?.length > 0 && (
                                                                    <span className={'text-white'}>{["+",item?.categories?.slice(2)?.length ?? 0].join("")}</span>
                                                                )
                                                            }

                                                        </div>
                                                    )
                                                }
                                            </div>
                                            <div className="">
                                                <Link to={`/travel/p/${item?.slug}`}>
                                                    <h3 className={'text-white p-0 m-0 leading-6'}>{item?.name ?? item?.roomName ?? item?.room_name ?? "-"}</h3>
                                                </Link>
                                                <span className={'text-white p-0 m-0 text-xs'}>{item?.slug}</span>
                                            </div>
                                            <div className="flex justify-between">
                                                {
                                                    !item?.is_free  ? (
                                                        <p className={'text-white'}>{[Utils.numberFormat(item?.price?.regular) ?? 0, 'day'].join("/")}</p>
                                                    ):(
                                                        <span className={'text-white'}>FREE</span>

                                                    )
                                                }

                                                {!item?.is_free  && (
                                                    <p className={'text-white'}>{item?.currency ?? "-"}</p>
                                                )}

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }

                </div>
            </div>
        </div>
    )
}
HomeTravelNearby.propTypes = {
    _getTravelNearby: PropTypes.func.isRequired,
    title: PropTypes.string,
    className: PropTypes.string,
    style: PropTypes.shape({}),
    notIn: PropTypes.array
}
HomeTravelNearby.defaultProps = {
    title: "Travel Nearby",
    className: "",
    style: {},
    notIn: [],
    _getTravelNearby: (body = {}, params = {}) => {
        console.log({body, params})
    }
}

export default connect(({Geo, Travel}) => {
    let {current} = Geo
    let {collections} = Travel
    let {nearby} = collections
    return {...current, nearby}
}, {_getTravelNearby})(React.memo(HomeTravelNearby))