            {/*                                            src={Array.isArray(item?.images) && item?.images?.length > 0 ? [item?.images[0]?.prefix_url, item?.images[0]?.filename].join("/") : 'https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png'}/>*/}
            {/*                                    }*/}
            {/*                                    actions={[*/}
            {/*                                        <SettingOutlined key="setting"/>,*/}
            {/*                                        <EditOutlined key="edit"/>,*/}
            {/*                                        <EllipsisOutlined key="ellipsis"/>,*/}
            {/*                                    ]}*/}
            {/*                                >*/}
            {/*                                    <Card.Meta*/}
