            {/*                                            style={{*/}
            {/*                                                color: '#f56a00',*/}
            {/*                                                backgroundColor: '#fde3cf',*/}
            {/*                                            }}>*/}
            {/*                                            {Array.isArray(item?.images) && item?.images?.length > 0 ? "" : item?.name.slice(0, 1)}*/}
