const HomePage = (props) => {
    const [visible, setVisible] = useState(false)
    const [visibleImage, setVisibleImage] = useState(false);
    const [selectedImage,setSelectedImage] = useState([])

    const [active,setActive] = useState('rooms')
    const handleTabs = (val)=> {
        setActive(val)
    }
    return (
        <LayoutContent style={{marginTop: 120, minHeight: "calc(100vh)"}}>
            <PageHeaderAlt background="/img/others/img-12.jpg" cssClass="bg-primary" overlap>
                <div className="container text-center">
                    <div className="py-5 my-md-5">
                    </div>
                </div>
            </PageHeaderAlt>
            <div className="container my-4">
                <div style={{width: "100%", maxWidth: 1080, margin: "0 auto"}}>
                    <Card bordered={false}>
                        <Tabs defaultActiveKey="rooms" onChange={handleTabs}>
                            <Tabs.TabPane tab="Room" key="rooms">
                                <CompSearchRoom {...props} setVisible={setVisible}/>
                            </Tabs.TabPane>
                            <Tabs.TabPane tab="Travel" key="travel" disabled>
                                <CompSearchTravel {...props}/>
                            </Tabs.TabPane>
                            <Tabs.TabPane tab="Villa" key="villa" disabled>
                                <CompSearchVilla {...props} setVisible={setVisible}/>
                            </Tabs.TabPane>
                        </Tabs>
                    </Card>
                </div>
                <div style={{width: "100%", maxWidth: 1080, margin: "0 auto"}}>
                    <CompResultRooms
                        {...props}
                        show={active === "2"}
                        visibleImage={visibleImage}
                        setVisibleImage={setVisibleImage}
                        selectedImage={selectedImage}
                        setSelectedImage={setSelectedImage}
                    />

                    {
                        /**
                         * TRAVEL NEARBY
                         */
                    }
                    <Row gutter={24}>
                        <Col xs={24} lg={16}>
                            <TravelNearby
                                {...props}
                                position={'left'}
                                className={''}
                                style={{}}
                            />
                        </Col>
                    </Row>

                    <HomeTravelNearby/>

                    <HomeTravelRecomendations notIn={[]}/>

                </div>

            </div>
        </LayoutContent>
    )
}

HomePage.propTypes = {}
HomePage.defaultProps = {}

export default connect(({Room}) => {
    let {collections} = Room
    let {search} = collections
    return {...search}
}, {})(React.memo(HomePage))
